:root {
  --solana-purple: #9945ff;
  --solana-green: #14f195;
  --solana-dark: #0b0f1a;
  --solana-card: #111827;
  --solana-border: #ffffff14;
  --solana-text: #e5e7eb;
  --solana-muted: #9ca3af;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: radial-gradient(circle at top, #1a1f3c, var(--solana-dark));
  min-height: 100vh;
  color: var(--solana-text);
  justify-content: center;
  align-items: center;
  font-family: Inter, Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
}

h1 {
  text-align: center;
  background: linear-gradient(90deg, var(--solana-purple), var(--solana-green));
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  margin-bottom: 30px;
  font-size: 2rem;
}

.section {
  border: 1px solid var(--solana-border);
  background: linear-gradient(#ffffff0a, #ffffff05);
  border-radius: 14px;
  padding: 20px;
  transition: transform .2s, box-shadow .2s;
  box-shadow: 0 10px 25px #0006;
}

.section:hover {
  transform: translateY(-4px);
  box-shadow: 0 15px 35px #0009;
}

.section h2 {
  color: var(--solana-green);
  align-items: center;
  gap: 8px;
  margin: 0 0 15px;
  font-size: 1.1rem;
  font-weight: 600;
  display: flex;
}

label {
  color: var(--solana-muted);
  font-size: .9rem;
}

input {
  border: 1px solid var(--solana-border);
  width: 180px;
  color: var(--solana-text);
  background: #0f172a;
  border-radius: 10px;
  margin-top: 6px;
  padding: 10px;
  transition: border-color .2s, box-shadow .2s;
}

input:focus {
  border-color: var(--solana-green);
  outline: none;
  box-shadow: 0 0 0 2px #14f19540;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 12px;
  align-items: center;
  gap: 6px;
  margin-top: 10px;
  padding: 10px 16px;
  font-weight: 600;
  transition: transform .15s, box-shadow .15s;
  display: inline-flex;
}

button:hover {
  transform: translateY(-2px);
}

#connectBtn {
  background: linear-gradient(135deg, var(--solana-purple), var(--solana-green));
  color: #000;
}

#createMintBtn {
  color: #fff;
  background: #6a43ff;
}

#mintBtn {
  background: var(--solana-green);
  color: #000;
}

#transferBtn {
  color: #000;
  background: #fbbf24;
}

#burnBtn {
  color: #fff;
  background: #ef4444;
}

#createAccountBtn {
  color: #fff;
  background: #8b5cf6;
}

#refreshMintBtn {
  color: #fff;
  background: #0ea5e9;
}

pre {
  background: #0f172a;
  border-radius: 8px;
  padding: 10px;
  overflow-x: auto;
}

code {
  display: inline-block;
  overflow-x: auto;
}

.icon {
  fill: currentColor;
  width: 16px;
  height: 16px;
}

#dashboard {
  border-radius: 16px;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 30px;
}

#sectionsContainer {
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  width: 100%;
  display: grid;
}

#mintResult2, #mintResult, #transferResult, #burnResult, #mintInfo, #walletInfo, #accountResult, code {
  border: 1px solid var(--solana-border);
  word-break: word-break;
  overflow-wrap: anywhere;
  white-space: normal;
  background: #0f172a;
  border-radius: 10px;
  max-width: 100%;
  margin-top: 10px;
  padding: 10px;
  font-size: .85rem;
  line-height: 1.3;
}

#destWallet {
  width: 100%;
}

#sectionsContainer .section:last-child {
  text-align: center;
  flex-direction: column;
  grid-column: 1 / -1;
  align-items: center;
  display: flex;
}

@media (width <= 900px) {
  input {
    width: 100%;
  }

  button {
    justify-content: center;
    width: 100%;
  }
}

@media (width <= 650px) {
  input {
    width: 100%;
  }

  button {
    justify-content: center;
    width: 100%;
  }
}

@media (width <= 540px) {
  #sectionsContainer {
    grid-template-columns: 1fr;
  }

  body {
    margin: 10px;
  }

  #dashboard {
    padding: 10px;
  }

  h1 {
    font-size: 1.4rem;
  }

  .section {
    padding: 12px;
  }

  button {
    justify-content: center;
    width: 100%;
  }

  input {
    width: 100%;
    font-size: 16px;
  }
}
